.bubble {
  position: relative;
  display: inline-block;
  overflow: hidden;
  color: #ffffff;
  font-family: "ARS", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border-radius: 25%/50%;
}

@media (max-width: 999px) {
  .bubble {
    font-size: 16px;
    line-height: 22px;
  }
}

@media (max-width: 639px) {
  .bubble {
    font-size: 15px;
    line-height: 20px;
  }
}

@media (max-width: 399px) {
  .bubble {
    font-size: 13px;
    line-height: 16px;
  }
}

.bubble--less-rounded {
  border-radius: 25%;
}

.bubble--elevated {
  box-shadow: 7px 7px 20px 0 rgba(0, 0, 0, 0.1);
}

.bubble--color-orange {
  color: #fd5300;
}

.bubble--picto {
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  border-radius: 25%;
  border-top-left-radius: 0 !important;
}

.bubble--picto .bubble__container {
  padding: 35px 30px;
}

.bubble--picto svg {
  display: block;
  width: 90px;
  height: auto;
  margin: 0 auto 25px;
}

.bubble--bg-orange {
  background-color: #fd5300;
  border-top-left-radius: 0 !important;
}

.bubble--bg-orange.bubble--transparent::after {
  background-color: #fd5300;
}

.bubble--bg-white {
  color: #fd5300;
  background-color: #ffffff;
}

.bubble--bg-white.bubble--transparent::after {
  background-color: #ffffff;
}

.bubble--bg-blue {
  background-color: #009db8;
  border-top-right-radius: 0 !important;
}

.bubble--bg-blue.bubble--transparent::after {
  background-color: #009db8;
}

.bubble--transparent {
  background-color: transparent;
  /*@supports (mix-blend-mode: multiply){
            &::before{
                content:"";
                z-index: 0;
                position: absolute;
                top:0;
                left: 0;
                width:100%;
                height:100%;
                background-color: rgba($white, 0.75);
            }

            &::after{
                opacity: 1;
                mix-blend-mode: multiply;
            }
        }*/
}

.bubble--transparent::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  content: "";
  opacity: 0.8;
}

.bubble--border-orange {
  border: 3px solid #fd5300;
}

.bubble--border-white {
  border: 3px solid #ffffff;
}

.bubble--corner-left {
  border-top-left-radius: 0 !important;
}

.bubble--corner-right {
  border-top-right-radius: 0 !important;
}

.bubble--bbloc .bubble__container, .bubble--bbloc-orange .bubble__container {
  padding-top: calc(28px/2 + 28px + 28px/2);
  padding-left: calc(40px/2 + 28px/2);
}

