.bubble__container {
  position: relative;
  z-index: 10;
  padding: 35px 30px 30px 35px;
}

@media (max-width: 639px) {
  .bubble__container {
    padding: 25px 30px;
  }
}

@media (max-width: 399px) {
  .bubble__container {
    padding: 14px 27px 16px 18px;
  }
}

.conversation__root {
  position: relative;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
}

.conversation__root .bubble--bg-orange,
.conversation__root .bubble--align-left {
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.conversation__root .bubble + .bubble {
  margin-top: 30px;
}

@media (max-width: 639px) {
  .conversation__root .bubble + .bubble {
    margin-top: 20px;
  }
}

@media (max-width: 399px) {
  .conversation__root .bubble + .bubble {
    margin-top: 14px;
  }
}

.conversation--animate .bubble, .conversation--animate-simple .bubble {
  opacity: 0;
}

.conversation__textarea {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 10px;
  font-size: 15px;
  background-color: #ffffff;
  border: 5px solid #eaeaea;
}

.conversation2 {
  margin: 70px 0;
  background-size: cover;
}

.conversation2 .conversation {
  padding: 5% 0;
  border: 0 none;
}

@keyframes loading {
  0% {
    top: 0;
  }
  30% {
    top: -8px;
  }
  60% {
    top: 0;
  }
}

.bubble__loader {
  position: absolute;
  top: 26px;
  left: 80px;
  display: block;
  transition: visibility 0.3s, opacity 0.3s;
  opacity: 0;
  visibility: hidden;
}

.bubble__loader span {
  position: relative;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #ffffff;
  border-radius: 50%;
  animation-delay: 0.25s;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}

.bubble__loader::before {
  position: absolute;
  left: -16px;
  width: 8px;
  height: 8px;
  content: "";
  background-color: #ffffff;
  border-radius: 50%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}

.bubble__loader::after {
  position: absolute;
  top: 0;
  left: 16px;
  width: 8px;
  height: 8px;
  margin-right: 10px;
  content: "";
  background-color: #ffffff;
  border-radius: 50%;
  animation-delay: 0.5s;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}
